#PE_hero_bg {
  background-color: #663399;
  overflow-y: hidden;

  @include respond-to('medium') {
    border-bottom: solid 5px #411F62;
    overflow-y: visible;
  }

  hgroup {
    max-width: 100%;

    @include respond-to('medium') {
      max-width: 50%;
    }
  }
}

#PE_Hero_img {
  background: url("../images/SagePartnerEdition/PE_Hero.png");
  background-size: cover;
  background-repeat: no-repeat;
  height: 280px;
  width: 100%;
  right: 0px;
  position: relative;
  top: 0px;
  @include box-shadow(5px 10px 60px 0px rgba(0,0,0,0.3));

  @include respond-to('medium') {
    border-radius: 0 0 4px 4px;
    height: 336px;
    width: 550px;
    position: absolute;
  }

  @include respond-to('large') {
    height: 400px;
    width: 772px;
  }

  @include respond-to('extralarge') {
    background: url("../images/SagePartnerEdition/PE_Hero_900w.png");
    background-size: cover;
    height: 550px;
    width: 900px;
    left: 53.5vw;
  }
}

#PE_Overview {
  article {
    padding-bottom: 100px;
  }
}

#PE_Challenge {
  background-color: #F9F9F9;
  z-index: -21;
  overflow: hidden;

  article {
    padding-bottom: 225px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }

    @include respond-to('large') {
      padding-bottom: 180px;
    }
  }

  img {
    bottom: -50px;
    height: auto;
    left: 50%;
    transform: translateX(-50%);
    width: 95vw;
    position: absolute;

    @include respond-to('medium') {
      max-width: 738px;
      left: 0;
      transform: translateX(0);
      right: 50.5vw;
      top: 170px;
      width: 52vw;
    }

    @include respond-to('large') {
      top: 82px;
    }
  }

  #PE_Challenge_bottombg {
    background-color: #EDEDED;
    bottom: 0;
    display: block;
    height: 60px;
    width: 100vw;
    height: 90px;
    position: absolute;
    z-index: -20;

    @include respond-to('large') {
      height: 90px;
    }
  }
}

#PE_sketching {
  background: url("../images/SagePartnerEdition/PE_Sketching.png") no-repeat center top;
  background-size: cover;
  min-height: 508px;

  #PE_Sketching_Shadow {
    @include linear-gradient(rgba(0,0,0,0.3) 0, rgba(0,0,0,0) 100%, 180deg);
  }

  article {
    padding-bottom: 250px;
  }

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.7) 40%, rgba(0,0,0,0.3) 100%, 135deg);
    background-size: cover;
  }
}

#PE_Prototyping {
  background-color: white;

  article {
    padding-bottom: 100px;
  }

  #PE_Proto_Container {
    height: 282px;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    width: 100%;
    z-index: 1;
    @include box-shadow(0px 10px 20px 0px rgba(0,0,0,0.25));
    @include border-radius(4px);

    @include respond-to('medium') {
      height: 564px;
      width: 900px;
    }

    @include respond-to('large') {
      height: 640px;
      width: 1024px;
    }

    @include respond-to('extralarge') {
      height: 780px;
      width: 1280px;
    }

    img {
      display: block;
      height: 48px;
      position: relative;
      width: auto;
      top: 0;
      left: 0;
    }

    video {
      display: block;
      top: 0px;
      left: 0px;
      position: relative;
      width: 100%;
      height: auto;
    }
  }
}

#PE_Guerilla_Testing {
  background: url("../images/SagePartnerEdition/PE_GuerillaTesting.png") no-repeat right center;
  background-size: cover;
  min-height: 718px;

  #PE_Guerilla_Shadow {
    @include linear-gradient(rgba(0,0,0,0.2) 0, rgba(0,0,0,0) 100%, 180deg);
  }

  article {
    padding-bottom: 310px;
  }

  p {
    width: 70%;

    @include respond-to('medium') {
      width: 100%;
    }
  }

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.8) 30%, rgba(0,0,0,0.3) 100%, 135deg);
    background-size: cover;
  }
}

#PE_Dialog {
  background-color: #F8F8F8;
  z-index: -23;

  article{
    padding-bottom: 50px;
  }

  #PE_dialog_img {
    display: block;
    background: url("../images/SagePartnerEdition/PE_Dialog.png");
    background-size: cover;
    height: 408px;
    left: -10vw;
    width: 1000px;
    position: relative;

    @include respond-to('large') {
      height: 707px;
      width: 1733px;
      left: 1.5vw;
    }

    @include respond-to('extralarge') {
      left: 15vw;
    }
  }

  #PE_dialog_bottombg {
    background-color: #FFF;
    bottom: 0;
    display: block;
    width: 100vw;
    height: 90px;
    position: absolute;
    z-index: -22;

    @include respond-to('large') {
      height: 180px;
    }
  }
}

#PE_Sidebar {
  background-color: #FFF;
  height: 1200px;
  overflow: hidden;

  @include respond-to('large') {
    height: 860px;
  }

  article {
    padding-bottom: 0px;

    @include respond-to('medium') {
      padding-top: 200px;
    }

    .left_wrapper {
      @include respond-to('medium') {
        width: 40%;
      }
    }
  }

  #PE_sidebar_img {
    background: url("../images/SagePartnerEdition/sidebar.png");
    background-size: contain;
    background-repeat: no-repeat;
    display: inline-block;
    height: 1047px;
    position: absolute;
    right: 0;
    top: 400px;
    width: 850px;


    @include respond-to('medium') {
      top: 450px;
    }

    @include respond-to('large') {
      top: 380px;
    }

    @include respond-to('extralarge') {
     top: 50px;
   }
 }

 #PE_Sidebar_Shadow {
  bottom: 0;
  @include linear-gradient(rgba(0,0,0,0.06) 0%, rgba(0,0,0,0) 90%, 360deg);
}
}

#PE_Interactions {
  background-color: #F8F8F8;
  z-index: -24;

  article {
    padding-bottom: 80px;
  }

  #PE_interactions_img {
    background: url("../images/SagePartnerEdition/PE_Interactions.png");
    background-size: cover;
    display: block;
    height: 394px;
    margin: 0 auto;
    width: 900px;

    @include respond-to('large') {
      height: 502px;
      width: 1147px;
    }
  }

  #PE_interactions_bottombg {
    background-color: #FFF;
    bottom: 0px;
    display: block;
    height: 100px;
    width: 100%;
    position: absolute;
    z-index: -25;
  }
}

#PE_Outcome {
  article {
    padding-top: 25px;
    padding-bottom: 0;
  }

  img {
    bottom: -3px;
    margin-top: 80px;
    position: relative;
    width: 100vw;
    height: auto;
  }
}
