.nav-container {
  background: rgba(255, 255, 255, 0.95);
  height: 44px;
  width: 100vw;
  position: fixed;
  z-index: 1111111;

  .nav-shadow {
    @include linear-gradient(rgba(0,0,0,0.3) 0, rgba(0,0,0,0) 100%, 180deg);
    height: 10px;
    opacity: 0;
    visibility: hidden;
  }

  &.nav-scrolling {

    .nav-shadow {
      border-top: 1px solid #E6EBED;
      transition: opacity 0.5s ease-in-out;
      opacity: 1;
      visibility: visible;
    }
  }

  .nav-inner {
    height: 100%;
    margin: 0 auto;
    padding: 0 2.5vw 0 2.5vw;
    width: 95vw;

    @include respond-to('medium') {
      width: 82vw;
      max-width: 1440px;
      min-width: 992px;
    }
  }

  .home, .casestudies {
    color: rgba(0,0,0,0.5);
    cursor: pointer;
    display: inline-block;
    height: 44px;
    font-size: 16px;
    font-weight: 600;
    padding: 12px 0;
    text-decoration: none;

    &:hover {
      color: rgba(0,0,0,0.9);
    }
  }

  .home {
    float: left;
  }

  .casestudies {
    float: right;
  }
}