#msoft_hero_bg, #vid_overlay {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 73vh;
  min-height: 550px;
  max-height: 764px;
  overflow: hidden;

  video {
    width: 100vw;
    height: 56.25vw;
    min-height: 100vh;
    min-width: 177.77vh;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -60%);
    z-index: -2;
    margin: 0;
  }

  #vid_overlay {
    @include linear-gradient(rgba(0,0,0,0.7) 30%, rgba(0,0,0,0.3) 60%, 135deg);
    background-size: cover;
    z-index: 1;
  }

  hgroup{
    max-width: 60%;
  }
}

#msoft_brainstorming {
  background: url("../images/Microsoft/IdeaGeneration.png") no-repeat center center fixed;
  background-size: cover;

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.8) 15%, rgba(0,0,0,0.1) 100%, 135deg);
    background-size: cover;
  }

  article {
    padding-bottom: 175px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }
  }
}

#msoft_sketching {
  article {
    padding-bottom: 400px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }

    img {
      left: 0px;
      position: absolute;
      top: 550px;
      width: 100vw;

      @include respond-to('medium') {
        width: 100%;
        margin-left: -3%;
        left: 0;
        position: relative;
        top: 0;
      }
    }
  }
}

#gadgeteer {
  overflow: hidden;

  article {
    padding-bottom: 0;

    img {
      width: 100%;
      bottom: 0;
      margin: 100px 0 -0.5% 0;
    }
  }
}

#msoft_makermark {
  background: url("../images/Microsoft/MakersMarks.png") no-repeat center bottom;
  background-size: cover;

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.6) 30%, rgba(0,0,0,0) 90%, 140deg);
    background-size: cover;
  }

  article {
    min-height: 458px;

    p {
      width: 58%;

      @include respond-to('medium') {
        width: 100%;
      }
    }
  }
}

#msoft_theapp {
  #theApp_Shadow {
    top: 0;
    @include linear-gradient(rgba(0,0,0,0.4) 0, rgba(0,0,0,0) 90%, 180deg);
  }

  background: #3E3D3A;
  background-size: cover;
  overflow: hidden;

  article {
    padding-bottom: 380px;

    @include respond-to('medium') {
      padding-bottom: 0;
    }

    img {
      position: absolute;
      bottom: 0px;
      width: 60%;
      left: 20vw;

      @include respond-to('medium') {
        margin: 0 0 -0.5% 25%;
        position: relative;
        width: 77%;
        left: initial;
      }
    }
  }
}

#msoft_finalpres {
  img {
    bottom: -3px;
    position: relative;
    width: 203vw;
    height: auto;

    @include respond-to('medium') {
      width: 100vw;
    }
  }
}
