@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700');

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  background-color: white;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}

h1, h2, h3, h4, h5, p {
  color: white;
}

// Variables for different breakpoint mixin
$breakpoints: (
  'small':  ( min-width:  320px ),
  'medium': ( min-width:  992px ),
  'large':  ( min-width: 1200px ),
  'extralarge': ( min-width: 1600px )
) !default;

article {
  height: 100%;
  margin: 0 auto;
  padding: 0 2.5vw 0 2.5vw;
  width: 95vw;

  @include respond-to('medium') {
    width: 82vw;
    max-width: 1440px;
    min-width: 992px;
  }
}

header {
  margin-top: 44px;
  position: relative;
}

section{
  position: relative;

  article {
    padding: 100px 2.5vw 100px 2.5vw;

    @include respond-to('medium') {
      padding-bottom: 150px;
  }

  @include respond-to('extralarge') {
      padding-top: 125px;
  }
  }
}

h1 {
  color: rgba(0,0,0,0.9);
  font-size: 32px;
  font-weight: 300;
  line-height: 36px;
  padding-top: 100px;

  @include respond-to('medium') {
    font-size: 40px;
    line-height: 48px;
    padding-top: 60px;
  }

  span {
    font-weight: 700;
  }

  &.casestudy-header {
    font-weight: 700;
    padding: 120px 0 1px 0;
    color: white;

    @include respond-to('medium') {
      padding: 96px 0 1px 0;
    }
  }
}

h2 {
  color: rgba(255,255,255,0.9);
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  margin-top: 40px;
  margin-bottom: 87px;
  padding-left: 2px;

  @include respond-to('medium') {
    font-size: 24px;
    margin-top: 25px;
    line-height: 28px;
  }
}

hgroup {
  display: inline-block;
  max-width: 50%;
  text-align: left;
  position: relative;
  z-index: 2;
}

h3 {
  font-weight: 600;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 20px;
  z-index: 101;

  @include respond-to('medium') {
    font-size: 28px;
    line-height: 24px;
  }
}

h4 {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  padding-top: 15px;
  z-index: 100;

  @include respond-to('medium') {
    font-size: 20px;
    line-height: 24px;
    padding-top: 15px;
  }
}

h5 {
  color: rgba(255,255,255,0.65);
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  padding-top: 5px;
  z-index: 100;

  @include respond-to('medium') {
    padding-top: 5px;
  }
}

p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

p.image_label {
  color: #9DA0A7;
  font-size: 12px;
  letter-spacing: 0.8px;
  font-weight: 700;
  line-height: 16px;
  text-transform: uppercase;
}

h1.dark_text, h2.dark_text, h3.dark_text, h4.dark_text, h5.dark_text {
  color: rgba(0,0,0,0.9);
}

p.dark_text {
  color: rgba(0,0,0,0.65);
}

.left_wrapper,
.right_wrapper {
  display: inline-block;
  text-align: left;
  width: 90%;
  vertical-align: top;

  @include respond-to('medium') {
    width: 47%;
  }
}

.left_wrapper {
  margin: 0 auto;

  @include respond-to('medium') {
    margin-right: 5.5%;
  }

  p {
    padding-top: 35px;
  }
}

.right_wrapper {
  p {
    padding-top: 50px;

    @include respond-to('medium') {
      padding-top: 35px;
    }
  }
}

.right_baseline_alignment {
  padding-top: 0px;

  @include respond-to('medium') {
    padding-top: 43px;
  }
}

.center_text_wrapper {
  text-align: left;
  margin-right: 5.5%;
  width: 90%;
  display: inline-block;
  height: 100%;
  vertical-align: top;

  @include respond-to('medium') {
    display: block;
    text-align: center;
    margin: 0 auto;
    width: 50%;
  }

  .dark_key_line, .light_key_line {
    margin: 0;

    @include respond-to('medium') {
      margin: 0 auto;
    }
  }

  //always centre this sub-header
  &.permacenter {
    display: block;
    text-align: center;
    margin: 0 auto;
    width: 50%;

    .dark_key_line, .light_key_line {
      margin: 0 auto;
    }
  }

  p {
    padding-top: 35px;
  }
}

.left_wrapper,
.right_wrapper,
.center_text_wrapper {
  h4{
    padding-bottom: 35px;

    @include respond-to('medium') {
      padding-bottom: 20px;
    }
  }
}

.dark_key_line,
.light_key_line {
  width: 50px;

  @include respond-to('medium') {
    width: 20px;
  }
}

.dark_key_line{
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.light_key_line{
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}

.center_image_wrapper {
  display: inline-block;
  width: 100%
}

.button_home {
  border-radius: 4px;
  height: 80px;
  left: 46px;
  position: absolute;
  transition: all 250ms ease-out;
  margin: 0;
  padding: 0;
  top: -4px;
  width: 80px;
  z-index: 50;

  @include respond-to('medium') {
    height: 48px;
    left: 29px;
    width: 44px;
  }

  @include respond-to('large') {
    height: 60px;
    width: 64px;
    transform: translateY(95px);
    left: -4px;
  }

  svg{
    width: 32px;
    height: 32px;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.7;

    @include respond-to('medium') {
      width: 16px;
      height: 16px;
    }
  }
}

.button_home:hover {
  transform: scale(1.15) perspective(1px) translateY(0px);

  @include respond-to('large'){
    transform: scale(1.1) perspective(1px) translateY(83px);
  }

  svg{
    opacity: 1;
  }
}

.light_home {
  background-color: rgba(255,255,255, 0.3);
}

.light_home:hover {
  background-color: rgba(255,255,255, 0.8);
}

.dark_home {
  background-color: rgba(0, 0, 0, 0.2);
}

.dark_home:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

// .quote_spacer{
//  margin-bottom: 70px;
// }

.quote {
  display: block;
  border-left: solid 3px #3592FF;
  padding-left: 20px;
  margin-bottom: 70px;

  p {
    padding: 0;
  }

  p.quoted_text {
    color: #9DA0A7;
    font-size: 0.9em;
    font-weight: 400;
    line-height: 1.5em;
    padding-bottom: 4px;
    position: relative;
    top: -6px;
  }

  p.long_quote_padding {
    padding-bottom: 25px;
  }

  p.quoted_person_text {
    color: #888;
    font-size: 0.70em;
    font-weight: 700;
    position: relative;
    bottom: -3px;
  }
}

// Adjust boxshadow per individual ID and apply this class
.topshadow {
  background-color: transparent;
  height: 25px;
  width: 100%;
  position: absolute;
  top: 0;
}

.bottomshadow {
  background-color: transparent;
  height: 25px;
  width: 100%;
  position: absolute;
  bottom: 0;
}
