.floating {
	animation-duration: 2.5s;
	-webkit-animation-duration: 2.5s;

	animation-iteration-count: infinite;
	-webkit-animation-iteration-count: infinite;

	animation-timing-function: ease-in-out;
	-webkit-animation-timing-function: ease-in-out;
}

#homepage_header {
	margin-top: 44px;

	article {
		padding: 70px 0;

		@include respond-to('medium') {
			padding-bottom: 25px;
		}
	}

	#cloud01_img {
		height: 27px;
		position: absolute;
		left: 8%;
		top: 20%;
		width: 117px;
		animation-name: floating1;
		-webkit-animation-name: floating1;

		@include respond-to('medium') {
			left: 18%;
		}

		@keyframes floating1 {
			0% {
				transform: translate(-18%, -20%);
			}
			50% {
				transform: translate(-18%, -25px);
			}
			100% {
				transform: translate(-18%, -20%);
			}
		}

		@-webkit-keyframes floating1 {
			0% {
				-webkit-transform: translate(-18%, -20%);
			}
			50% {
				-webkit-transform: translate(-18%, -25px);
			}
			100% {
				-webkit-transform: translate(-18%, -20%);
			}
		}
	}

	#cloud02_img {
		height: 48px;
		position: absolute;
		top: 9%;
		width: 117px;
		animation-name: floating2;
		-webkit-animation-name: floating2;

		@include respond-to('small') {
			left: 20%;
		}

		@include respond-to('medium') {
			left: 27%;
		}

		@keyframes floating2 {
			0% {
				transform: translate(-27%, -9%);
			}
			50% {
				transform: translate(-27%, -23px);
			}
			100% {
				transform: translate(-27%, -9%);
			}
		}

		@-webkit-keyframes floating2 {
			0% {
				-webkit-transform: translate(-27%, -9%);
			}
			50% {
				-webkit-transform: translate(-27%, -23px);
			}
			100% {
				-webkit-transform: translate(-27%, -9%);
			}
		}
	}

	#cloud03_img {
		height: 49px;
		position: absolute;
		top: 18%;
		width: 121px;
		animation-name: floating3;
		-webkit-animation-name: floating3;

		@include respond-to('small') {
			right: -15%;
		}

		@include respond-to('medium') {
			right: 25%;
		}

		@keyframes floating3 {
			0% {
				transform: translate(-18%, -25%);
			}
			50% {
				transform: translate(-18%, -30px);
			}
			100% {
				transform: translate(-18%, -25%);
			}
		}

		@-webkit-keyframes floating3 {
			0% {
				-webkit-transform: translate(-18%, -25%);
			}
			50% {
				-webkit-transform: translate(-18%, -30px);
			}
			100% {
				-webkit-transform: translate(-18%, -25%);
			}
		}
	}

	#cloud04_img {
		height: 29px;
		position: absolute;
		top: 10%;
		right: 18%;
		width: 78px;
		animation-name: floating4;
		-webkit-animation-name: floating4;

		@include respond-to('small') {
			display: none;
		}

		@include respond-to('medium') {
			display: inline-block;
		}

		@keyframes floating4 {
			0% {
				transform: translate(-18%, -10%);
			}
			50% {
				transform: translate(-18%, -23px);
			}
			100% {
				transform: translate(-18%, -10%);
			}
		}

		@-webkit-keyframes floating4 {
			0% {
				-webkit-transform: translate(-18%, -10%);
			}
			50% {
				-webkit-transform: translate(-18%, -23px);
			}
			100% {
				-webkit-transform: translate(-18%, -10%);
			}
		}
	}

	#bike_Luke_img {
		bottom: 0px;
		display: block;
		height: auto;
		margin: 0 auto;
		position: relative;
		left: -5vw;
		width: 50vw;

		@media screen and (max-width: 650px) {
			width: 75vw;
		}

		@include respond-to('medium') {
			left: -35px;
			width: 50vw;
			max-height: 338px;
			max-width: 359px;
		}
	}
	.center_text_wrapper {
		display: block;
		text-align: center;
		margin: 0 auto;
		width: 80%;
	}

	p {
		padding-bottom: 25px;
		margin:  0 auto;

		@include respond-to('medium') {
			max-width: 505px;
		}
	}
}
