.footer_casestudies {
	background-color: #29292c;
	padding-top: 75px;

	a {
		position: relative;
		text-decoration: none;
		text-align: left;
	}

	.casestudy_thumbs {
		align-content: center;
		display: flex;
		flex-flow: row wrap;
		justify-content: space-around;
		margin: 0 auto;
		max-width: 1400px;
		padding: 45px 0 0 0;

		li {
			width: 85vw;
			margin: auto;
			padding: 0 15px 55px 15px;

			@include respond-to('medium') {
				width: 450px;
				padding: 0 15px 80px 15px;
			}
		}
	}
}

.Casestudies_Shadow {
	@include linear-gradient(rgba(0,0,0,0.3) 0, rgba(0,0,0,0) 100%, 180deg);
}


.casestudy_onboarding_thumb {
	background: url("../images/Homepage/thumb_onboarding.png");
