}

.casestudy_partneredition_thumb {
	background: url("../images/Homepage/thumb_sagepartneredition.png");
	background-size: cover;
}

.casestudy_contacts_thumb {
	background: url("../images/Homepage/thumb_sagecontacts.png");
	background-size: cover;
}

.casestudy_contentdiscovery_thumb {
	background: url("../images/Homepage/thumb_contentdiscovery.png");
	background-size: cover;
}

.casestudy_tv_thumb {
	background: url("../images/Homepage/thumb_TV.png");
	background-size: cover;
}

.casestudy_lloyds_thumb {
	background: url("../images/Homepage/thumb_productcomparison.png");
	background-size: cover;
}

.casestudy_msoft_thumb {
	background: url("../images/Homepage/thumb_microsoft.png");
	background-size: cover;
}

.index_thumb {
	background-color: black;
	border-radius: 6px;
	transition: all 250ms ease-out;
	// @include box-shadow(3px 5px 15px 0px rgba(0,0,0,0.4));
	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
	filter: brightness(90%) saturate(100%);
	padding-bottom: 56.25%;
	position: relative;
	width: 100%;

	@include respond-to('medium') {
		height: 250px;
	}
}

a:hover {
	.index_thumb {
		filter: brightness(100%) saturate(105%);
		transform: scale(1.05) perspective(1px) translateY(-5px);
		box-shadow: 0 14px 28px rgba(0,0,0,0.15), 0 10px 10px rgba(0,0,0,0.15);
	}
}

.contact_icons {
	border-top: 1px solid  rgba(255, 255, 255, 0.1);
	display: block;
	margin: 0 auto;
	max-width: 1354px;
	// min-width: 300px;
	padding-bottom: 80px;
	width: 95vw;;

	ul {
		margin: 0 auto;
		padding-top: 25px;

		@include respond-to('small') {
			width: 315px;
		}

		@include respond-to('medium') {
			width: 231px;
		}

		li {
			display: inline-block;
			transition: all 250ms ease-out;
			margin: 0 25px;
			opacity: 0.3;

			@include respond-to('medium') {
				margin: 0 15px;
			}

			a {
				height: 44px;
				width: 44px;
			}

			svg {
				position: relative;

				@include respond-to('small') {
					width: 32px;
					height: 32px;
				}

				@include respond-to('medium') {
					width: 24px;
					height: 24px;
				}
			}
		}

		li:hover {
			opacity: 1;
		}

		.email {
			padding: 11px 10px;

			svg{
				top: 3px;
			}
		}

		.linkedin {
			padding: 11px 10px;

			svg{
				top: 3px;
			}
		}

		.dribbble {
			padding: 11px 10px;

			svg{
				top: 4px;
			}
		}
	}
}