#SageContacts_hero_bg {
  background-color: #41A940;
  overflow-y: hidden;

  @include respond-to('medium') {
    border-bottom: solid 5px #248523;
    overflow-y: visible;
  }

  hgroup {
    max-width: 100%;

    @include respond-to('medium') {
      max-width: 50%;
    }
  }
}

#SageContacts_Hero_img {
  background: url("../images/SageContacts/Contacts_Hero.png");
  background-size: cover;
  background-repeat: no-repeat;
  height: 300px;
  width: 100%;
  position: relative;
  top: 0px;
  @include box-shadow(5px 10px 60px 0px rgba(0,0,0,0.3));

  @include respond-to('medium') {
    border-radius: 0 0 4px 4px;
    height: 438px;
    left: 54vw;
    position: absolute;
    width: 512px;
  }

  @include respond-to('large') {
    height: 530px; //653 for full screen
    width: 758px;
    left: 54vw;
  }

  @include respond-to('extralarge') {
    height: 600px;
    width: 960px;
    left: 58vw;
  }
}

#Contacts_Overview {
  article {
    padding-bottom: 75px;
  }
}

#Contacts_Callenge {
  background-color: #F9F9F9;
  bottom: -80px;
  z-index: -11;

  article {
    padding-bottom: 50px;
  }

  #Contacts_Challenge_img {
    background: url("../images/SageContacts/Contacts_Challenge.png");
    background-size: cover;
    width: 200vw;
    height: 400px;
    right: 100vw;
    position: relative;
    z-index: 12;

    @include respond-to('medium') {
      height: 490px;
      width: 972px;
      margin: 0 auto;
      right: inherit;
    }

    @include respond-to('large') {
      height: 621px;
      width: 1233px;
    }


    @include respond-to('extralarge') {
      height: 705px;
      width: 1400px;
    }
  }
}

#Contacts_Challenge_bottombg {
  background-color: #EDEDED;
  bottom: 0;
  height: 325px;
  width: 100%;
  position: absolute;
  z-index: -12;

  @include respond-to('large') {
    height: 470px;
  }
}

#Contacts_sketching {
  background: url("../images/SageContacts/Contacts_Sketching.png") no-repeat center top fixed;
  background-size: cover;

  article{
    padding-bottom: 250px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }
  }

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.8) 40%, rgba(0,0,0,0.5) 100%, 135deg);
    background-size: cover;
  }
}

#Contacts_List_Concept {
  article {
    padding-bottom: 100px;
  }

  img {
    display: block;
    height: auto;
    width: 200vw;
    position: relative;

    @include respond-to('medium') {
      width: 100%;
      max-height: 1920px;
    }
  }
}

#Contacts_Cards {
  background-color: #F8F8F8;

  #Contacts_List_Shadow {
    @include linear-gradient(rgba(0,0,0,0.08) 0, rgba(0,0,0,0) 100%, 180deg);
  }

  article {
    padding-bottom: 80px;
  }

  img {
    display: block;
    height: auto;
    left: -288px;
    padding-bottom: 90px;
    position: relative;
    width: 271vw;

    @include respond-to('medium') {
      left: inherit;
      margin: 0 auto;
      max-width: 1728px;
      width: 90vw;
    }
  }
}

#Contacts_Individuals {

  article {
    padding-bottom: 100px;
  }

  img {
    display: block;
    height: auto;
    width: 200vw;
    position: relative;

    @include respond-to('medium') {
      width: 100%;
      max-height: 1920px;
    }
  }
}

#Contacts_React {
  background-color: #292E37;
  z-index: -15;

  article {
    padding-bottom: 50px;
  }

  img {
    display: block;
    height: auto;
    margin: 0 auto;
    width: 200vw;
    right: 2vw;
    position: relative;

    @include respond-to('medium') {
      max-width: 1075px;
      right: inherit;
      width: 56vw;
    }
  }
}

#Contacts_React_bottombg {
  background-color: #F8F8F8;
  bottom: 0;
  height: 180px;
  width: 100%;
  position: absolute;
  z-index: -16;

  @include respond-to('medium') {
    height: 150px;
  }

  @include respond-to('large') {
    height: 200px;
  }

  @include respond-to('extralarge') {
    height: 280px;
  }
}

#Contacts_Adaptive_Cards {
  background-color: #F8F8F8;
  z-index: -18;

  article {
    padding-bottom: 70px;
  }
}

.Contacts_Card_Wrapper {
  padding-bottom: 100px;
  z-index: -19;

  p {
    padding-bottom: 25px;
    text-align: center;
  }

  img {
    display: block;
    height: auto;
    width: 95vw;
    margin: 0 auto;

    @include respond-to('medium') {
      max-width: 1536px;
      width: 80vw;
    }
  }

  #Contacts_noaddress_img {
    height: auto;
    width: 67vw;
    max-width: 1286px;
  }
}


#Contacts_cards_bottombg {
  background-color: #3C424F;
  height: 125px;
  bottom: 0;
  width: 100%;
  position: absolute;
  z-index: -17;

  @include respond-to('medium') {
    height: 175px;
  }

  #Individual_Contacts_Shadow {
    @include linear-gradient(rgba(0,0,0,0.3) 0, rgba(0,0,0,0) 90%, 180deg);
  }
}


#Contacts_Outcome {
  background-color: #3C424F;
  z-index: -14;

  article{
    padding-bottom: 30px;
    padding-top: 0px;

    @include respond-to('medium') {
      padding-bottom: 80px;
    }
  }

  #Contacts_Outcome_img {
    background: url("../images/SageContacts/Contacts_MVP.png");
    background-size: cover;
    background-repeat: no-repeat;
    height: 350px;
    width: 266vw;
    position: relative;
    right: 160vw;

    @include respond-to('medium') {
      height: 380px;
      margin: 0 auto;
      width: 1000px;
      right: inherit;
    }

    @include respond-to('large') {
      height: 490px;
      width: 1289px;
      right: 30px;
    }

    @include respond-to('extralarge') {
      height: 570px;
      width: 1500px;
      right: 55px;
    }
  }
}

#Contacts_Outcome_bottombg {
  background-color: #292E37;
  bottom: 0;
  height: 50px;
  width: 100%;
  position: absolute;
  z-index: -13;

  @include respond-to('large') {
    height: 65px;
  }

  @include respond-to('extralarge') {
    height: 75px;
  }
}
