#lloyds_hero_bg {
  background-color: #003BBE;
  border-bottom: solid 5px #00ACF2;
  height: 540px;
  z-index: -1;

  @include respond-to('medium') {
    height: 336px;
  }

  img {
    height: auto;
    right: -15vw;
    position: relative;
    top: -70px;
    width: 60vw;
    z-index: 3;

    @include respond-to('medium') {
      width: 75%;
      margin: 5% 0 -100% 30%;
      right: inherit;
      top: inherit;
    }
  }

  hgroup {
    max-width: 100%;

    @include respond-to('medium') {
      max-width: 50%;
    }
  }

  #trianglebg {
    border-bottom: 200px solid #1A4EC4;
    border-left: 70vw solid transparent;
    border-right: 0px solid transparent;
    height: 0;
    width: 0;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 0;
  }
}

#Lloyds_overview{
  article {
    padding-top: 180px;

    @include respond-to('medium') {
      padding-top: 100px;
    }
  }
}

#lloyds_sketching_bg {
  background: url("../images/Lloyds/SketchingHero.png") no-repeat center top fixed;
background-size: cover;

.gradient_overlay {
  @include linear-gradient(rgba(0,0,0,0.8) 15%, rgba(0,0,0,0.2) 100%, 135deg);
  background-size: cover;
}

article {
  padding-bottom: 225px;

  @include respond-to('medium') {
    padding-bottom: 100px;
  }
}
}

#lloyds_concepts {
  overflow: hidden;

  article {
    padding-bottom: 0;

    p {
      padding-bottom: 35px;
    }

    img {
      width: 150%;
      bottom: 0;
      padding-top: 4%;
      margin-bottom: -0.5%;

      @include respond-to('medium') {
        width: 100%;
      }
    }
  }
}

#lloyds_research_bg {
  background: url("../images/Lloyds/UsabilityTesting.png") no-repeat right center fixed;
  background-size: cover;

  @include respond-to('medium') {
    background: url("../images/Lloyds/UsabilityTesting.png") no-repeat center center fixed;
    background-size: cover;
  }

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.8) 15%, rgba(0,0,0,0.0) 100%, 140deg);
    background-size: cover;
  }

  article {
    padding-bottom: 250px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }
  }
}


#lloyds_final_bg {

  article {
    padding-bottom: 4%;
  }

  ul {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: center;
    margin: 0 auto;
    padding: 25px 0 3% 0;
    max-width: 1110px;

    li {
      margin: 25px auto;

      @include respond-to('medium') {
        margin: auto;
      }

      p {
        height: 100px;
        width: 300px;
        text-align: center;
        text-decoration: none;
        margin: 0 auto;
        padding-top: 35px;

        @include respond-to('medium') {
          width: 190px;
        }
      }

      img {
        height: auto;
        width: 300px;

        @include respond-to('medium') {
          height: auto;
          width: 201px;
        }
      }
    }
  }
}
