@mixin box-shadow($param...) {
     -moz-box-shadow: $param;
  -webkit-box-shadow: $param;
          box-shadow: $param;
}

@mixin background-size($param) {
  -webkit-background-size: $param;
     -moz-background-size: $param;
       -o-background-size: $param;
          background-size: $param;
}

@mixin transition($param) {
  -webkit-transition: $param;
     -moz-transition: $param;
       -o-transition: $param;
          transition: $param;
}

@mixin border-radius($param) {
  -webkit-border-radius: $param;
     -moz-border-radius: $param;
          border-radius: $param;
}


@mixin box-sizing($param) {
  -webkit-box-sizing: $param;
     -moz-box-sizing: $param;
          box-sizing: $param;
}

@mixin appearance ($param) {
  -webkit-appearance: $param;
     -moz-appearance: $param;
          appearance: $param;
}

@mixin linear-gradient($fromColor, $toColor, $angle:top) {
  background: -webkit-gradient(linear, left top, left bottom, from($fromColor), to($toColor)); /* Saf4+, Chrome */
  background: -webkit-linear-gradient($angle, $fromColor, $toColor); /* Chrome 10+, Saf5.1+, iOS 5+ */
  background:    -moz-linear-gradient($angle, $fromColor, $toColor); /* FF3.6 */
  background:     -ms-linear-gradient($angle, $fromColor, $toColor); /* IE10 */
  background:      -o-linear-gradient($angle, $fromColor, $toColor); /* Opera 11.10+ */
  background:         linear-gradient($angle, $fromColor, $toColor);
}

@mixin radial-gradient($fromColor, $toColor) {
  background: $fromColor; /* Old browsers */
  background: -moz-radial-gradient(center, ellipse cover,  $fromColor 0%, $toColor 100%); /* FF3.6+ */
  background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%,$fromColor), color-stop(100%,$toColor)); /* Chrome,Safari4+ */
  background: -webkit-radial-gradient(center, ellipse cover,  $fromColor 0%,$toColor 100%); /* Chrome10+,Safari5.1+ */
  background: -o-radial-gradient(center, ellipse cover,  $fromColor 0%,$toColor 100%); /* Opera 12+ */
  background: -ms-radial-gradient(center, ellipse cover,  $fromColor 0%,$toColor 100%); /* IE10+ */
  background: radial-gradient(ellipse at center,  $fromColor 0%,$toColor 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#{$fromColor}', endColorstr='#{$toColor}',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

@mixin clear() {
  clear: both;

  &:before, &:after {
    content: "\0020";
    display: block;
    height: 0;
    overflow: hidden;
  }

  &:after {
    clear: both;
  }
}

@mixin hoverActiveFocus($property, $value) {
  &:hover, &:active, &:focus {
    #{$property}: $value;
  }
}

/* prefix declarations */
@mixin prefixed($property, $value) {
    -webkit-#{$property}: $value;
       -moz-#{$property}: $value;
        -ms-#{$property}: $value;
         -o-#{$property}: $value;
            #{$property}: $value;
}

@mixin animation($animate...) {
  $max: length($animate);
  $animations: '';

  @for $i from 1 through $max {
    $animations: #{$animations + nth($animate, $i)};

    @if $i < $max {
      $animations: #{$animations + ", "};
    }
  }

  -webkit-animation: $animations;
     -moz-animation: $animations;
       -o-animation: $animations;
          animation: $animations;
}

@mixin keyframes($animationName) {
  @-webkit-keyframes #{$animationName} {
    @content;
  }
  @-moz-keyframes #{$animationName} {
    @content;
  }
  @-o-keyframes #{$animationName} {
    @content;
  }
  @keyframes #{$animationName} {
    @content;
  }
}

@mixin animation-delay($value) {
          animation-delay: $value + s;
  -webkit-animation-delay: $value + s;
}

@mixin hidden() {
  display: none !important;
  opacity: 0 !important;
  visibility: hidden !important;
}

@mixin visible() {
  display: block !important;
  opacity: 1 !important;
  visibility: visible !important;
}

// generic transform
@mixin transform($transforms) {
     -moz-transform: $transforms;
       -o-transform: $transforms;
      -ms-transform: $transforms;
  -webkit-transform: $transforms;
          transform: $transforms;
}
// rotate
@mixin rotate ($deg) {
  @include transform(rotate(#{$deg}deg));
}

// scale
@mixin scale($scale) {
   @include transform(scale($scale));
}
// translate
@mixin translate ($x, $y) {
   @include transform(translate($x, $y));
}
// skew
@mixin skew ($x, $y) {
   @include transform(skew(#{$x}deg, #{$y}deg));
}
//transform origin
@mixin transform-origin ($origin) {
    moz-transform-origin: $origin;
       -o-transform-origin: $origin;
      -ms-transform-origin: $origin;
  -webkit-transform-origin: $origin;
          transform-origin: $origin;
}

// Placeholder for inputs
@mixin placeholder {
  &::-webkit-input-placeholder {@content}
  &:-moz-placeholder           {@content}
  &::-moz-placeholder          {@content}
  &:-ms-input-placeholder      {@content}
}

@mixin flexbox() {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}


// breakpoint mixin
@mixin respond-to($breakpoint) {
  // If the key exists in the map
  @if map-has-key($breakpoints, $breakpoint) {
    // Prints a media query based on the value
    @media #{inspect(map-get($breakpoints, $breakpoint))} {
      @content;
    }
  }
 
  // If the key doesn't exist in the map
  @else {
    @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Available breakpoints are: #{map-keys($breakpoints)}.";
  }
}
