#BBCSearch_hero_bg {
  background-color: #084b75;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 83vh;
  min-height: 550px;
  max-height: 764px;
  z-index: -1;
  // pointer-events: none;
  overflow: hidden;
  border-bottom: solid 5px #02CFD0;

  video {
    height: auto;
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 150vw;
    transform: translateX(-50%);
    z-index: -2;

    @include respond-to('large') {
      width: 100vw;
    }
    //min-height: 100vh;
    //min-width: 177.77vh;
  }

  hgroup {
    max-width: 100%;

    @include respond-to('medium') {
      max-width: 40%;
    }
  }
}

#CD_design_sprints {
  background: url("../images/ContentDiscovery/Sprints.png") no-repeat center bottom fixed;
  background-size: cover;

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.7) 60%, 35deg);
    background-size: cover;
  }

  article{
    padding-bottom: 250px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }

    @include respond-to('extralarge') {
      min-height: 550px;
    }
  }
}

#CD_concepting_cards {
  article {
    padding-bottom: 0;

    #trianglebg2 {
      height: 0;
      border-bottom: 15vh solid #F9F9F9;
      border-left: 140vw solid transparent;
      border-right: 0px solid transparent;
      position: absolute;
      right: 0;
      top: 500px;
      width: 0;
      z-index: -2;

      @include respond-to('medium') {
        top: 438px;
        border-bottom: 35vh solid #F9F9F9;
        border-left: 160vw solid transparent;
        border-right: 0px solid transparent;
      }
    }
  }
}

#concept_cards_img {
  background-image: url("../images/ContentDiscovery/ConceptingCards_LargeX2.png");
  background-size: contain;
  background-repeat: no-repeat;
  bottom: 0;
  height: 300px;
  position: relative;
  width: 120vw;
  right: 12vw;
  top: -0px;
  z-index: 3;

  @include respond-to('medium') {
    background-size: cover;
    height: inherit;
    left: -30px;
    width: 100%;
    margin: 0 auto;
    padding-top: (341 / 512) * 100%;
    top: -135px;
  }
}

#CD_multivariate_testing {
  article{
    padding-bottom: 8vh;

    @include respond-to('medium') {
      padding-top: 10vh;
    }
  }

  #CD_multivariate_bg {
    background: url("../images/ContentDiscovery/CDMultiVariateBG.png") no-repeat center bottom fixed;
    background-size: cover;
    bottom: 70px;
    display: block;
    height: 15vh;
    position: absolute;
    width: 100%;
    z-index: -3;

    @include respond-to('medium') {
      height: 40vh;
      max-height: 380px;
      min-height: 300px;
    }

    @include respond-to('large') {
      bottom: 176px;
    }
  }
}

#CD_multivariate_img {
  background-image: url("../images/ContentDiscovery/Multivariate_LargeX2.png");
  background-size: cover;
  margin: 0 auto;
  padding-top: (503 / 1024) * 100%;
  position: relative;
  top: -28px;
  width: 100%;
  z-index: 3;

  @include respond-to('large') {
    height: 601px;
    max-width: 1223px;
    padding: 0;
  }
}


#CD_usability_testing {
  article{
    padding-top: 4%;
    padding-bottom: 45vh;

    @include respond-to('medium') {
      padding-bottom: 3%;
    }

    h4{
      padding-top: 35px;
    }
  }


  #CD_usability_bg {
    background-color: #F9F9F9;
    bottom: 0;
    height: 200px;
    width: 100%;
    position: absolute;
    z-index: -5;

    @include respond-to('medium') {
      height: 100px;
    }

    @include respond-to('large') {
      height: 125px;
    }
  }

  #CD_prototype_img {
    background-image: url("../images/ContentDiscovery/Card_Prototype.gif");
    background-size: contain;
    background-repeat: no-repeat;
    height: 333px;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    top: 325px;
    width: 300px;
    margin: 0 auto;
    @include box-shadow(5px 10px 40px 0px rgba(0,0,0,0.3));

    @include respond-to('medium') {
      background-size: cover;
      position: relative;
      width: 334px;
      height: 372px;
      top: 0;
      left: 0;
      transform: translateX(0);
    }
  }
}

#CD_developer_guidelines {
  background-color: #F9F9F9;
  z-index: -5;

  article {
    padding-bottom: 0;

    h4 {
      padding-top: 100px;

      @include respond-to('medium') {
        padding-top: 35px;
      }
    }

    p {
      padding-bottom: 50px;

      @include respond-to('medium') {
        padding-bottom: 0px;
      }
    }
  }

  #CD_guidelines_img {
    background-image: url("../images/ContentDiscovery/DeveloperGuidelines.png");
    background-size: contain;
    background-repeat: no-repeat;
    height: 354px;
    width: 368px;
    margin: 0 auto;
    position: relative;

    @include respond-to('medium') {
      background-size: cover;
      height: 477px;
      width: 538px;
    }
  }

  #CD_guidelines_bottombg {
    background-color: #3C424F;
    bottom: 0;
    height: 200px;
    width: 100%;
    position: absolute;
    z-index: -4;

    @include respond-to('medium') {
      height: 115px;
    }
  }
}

#CD_Outcome {
  background-color: #3C424F;
  z-index: -7;
  padding-bottom: 0;

  article{
    padding-top: 100px;
    padding-bottom: 75px;


    @include respond-to('medium') {
      padding-top: 1%;
      padding-bottom: 5%;
    }
  }

  #CD_outcome_img {
    background-image: url("../images/ContentDiscovery/cd_outcomeX2.png");
    background-size: contain;
    background-repeat: no-repeat;
    height: 100vw;
    right: 90vw;
    position: relative;
    width: 200vw;

    @include respond-to('medium') {
      background-size: cover;
      height: 380px;
      width: 820px;
      margin: 0 auto;
      padding: 0;
      right: inherit;
    }

    @include respond-to('large') {
      height: 508px;
      width: 1095px;
    }
  }
}
