#TVPlatforms_hero_bg {
  background-color: #C80A24;
  overflow-y: hidden;

  @include respond-to('medium') {
    border-bottom: solid 5px #FF6E03;
    overflow-y: visible;
  }

  hgroup {
    max-width: 100%;

    @include respond-to('medium') {
      max-width: 50%;
    }
  }

  h2 {
    margin-bottom: 25px;

    @include respond-to('medium') {
      margin-bottom: 83px;
    }
  }
}

#Red_Button_Hero_img {
  background: url("../images/TVPlatforms/RedButtonHero.png");
  background-size: contain;
  background-repeat: no-repeat;
  height: 300px;
  width: 150vw;
  position: relative;

  @include respond-to('medium') {
    background-size: cover;
    height: 624px;
    width: 946px;
    position: absolute;
    left: 45vw;
    right: inherit;
    top: -101px;
  }

}

#TV_overview {
  article{
    padding-top: 50px;

    @include respond-to('medium') {
      padding-top: 150px;
    }
  }
}

#TV_sketching {
  background: url("../images/TVPlatforms/TV_sketching.png") no-repeat center top fixed;
  background-size: cover;

  .gradient_overlay {
    @include linear-gradient(rgba(0,0,0,0.8) 0%, rgba(0,0,0,0.4) 100%, 135deg);
    background-size: cover;
  }

  article {
    padding-bottom: 200px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }

     @include respond-to('extralarge') {
      min-height: 550px;
    }
  }
}

#TV_guidelines{
  article {
    padding-bottom: 360px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }
  }

  #TV_guidelines_img {
    background: url("../images/TVPlatforms/TV_guidelines.png");
    background-size: cover;
    height: 283px;
    width: 894px;
    position: absolute;
    left: 10vw;
    top: 415px;
    z-index: 10;

    @include respond-to('medium') {
      left: 56vw;
      top: 76px;
    }
  }
}

#TV_GEL {
  background-color: #F9F9F9;

  article {
    padding-bottom: 300px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }

    .left_wrapper {
      height: 0;

      @include respond-to('medium') {
        height: 370px;
      }
    }
  }

  #TV_GEL_img {
    background: url("../images/TVPlatforms/TV_GEL.png");
    background-size: contain;
    background-repeat: no-repeat;
    height: 250px;
    position: relative;
    width: 150vw;
    right: 46vw;
    top: 340px;

    @include respond-to('medium') {
      background-size: cover;
      height: 415px;
      width: 905px;
      padding: 0;
      position: absolute;
      right: 53vw;
      top: 60px;
    }
  }
}

#TV_Prototyping {
  background-color: #3C424F;
  height: 600px;

  @include respond-to('medium') {
    height: 530px;
  }
}

#Prototyping_Vid_Wrapper {
  position: relative;
  top: -120px;

  video {
    @include border-radius (2px);
    background-color: #000;
    border: solid 2px #000;
    box-shadow:
    0 0 0 6px #333,
    0 0 0 7px #222;
    display: block;
    height: auto;
    width: 100vw;
    position: relative;

    @include respond-to('medium') {
      right: 40px;
      width: 750px;
      margin: 0 auto;
    }

    @include respond-to('large') {
      width: 880px;
    }

   @include respond-to('extralarge') {
     width: 1024px;
     right: 60px;
   }
 }

  img {
    display: none;

    @include respond-to('medium') {
      bottom: 400px;
      display: inline-block;
      position: relative;
      height: auto;
      width: 600px;
      margin-left: 45%;
      z-index: 10;
    }

    @include respond-to('large') {
      bottom: 465px;
      width: 700px;
      margin-left: 50%;
    }

    @include respond-to('extralarge') {
      bottom: 536px;
      width: 800px;
      margin-left: 48%;
    }
  }
}

#diagonalextension1 {
  display: block;
  height: 0;
  border-top: 22vh solid #3C424F;
  border-left: 150vw solid transparent;
  border-right: 0 solid transparent;
  position: relative;
  top: 0;
  width: 0;
  z-index: -2;

  @include respond-to('medium') {
    border-top: 60vh solid #3C424F;
    border-left: 160vw solid transparent;
  }
}


#TV_Live_restart {
  padding-bottom: 50px;

  article{
    padding-top: 0;
    padding-bottom: 75px;

    @include respond-to('medium') {
      padding-top: 100px;
      padding-bottom: 50px;
    }
  }
}

#live_restart_img {
  background-image: url("../images/TVPlatforms/Live_Restart_x2.png");
  background-size: contain;
  background-repeat: no-repeat;
  height: 312px;
  margin-left: 35px;
  position: relative;
  width: 280vw;

  @include respond-to('medium') {
    background-size: cover;
    height: 387px;
    width: 984px;
  }

  @include respond-to('large') {
    height: 456px;
    width: 1158px;
    margin: 0 auto;
  }
}

#TV_restart_bottombg {
  background-color: #F9F9F9;
  bottom: 0;
  height: 300px;
  width: 100%;
  position: absolute;
  z-index: -9;

  @include respond-to('medium') {
   height: 400px;
 }
}

#TV_travel {
  background-color: #3C424F;
  height: 600px;

  @include respond-to('medium') {
    height: 525px;
  }
}

#tv_travel_img {
  background-image: url("../images/TVPlatforms/TV_Travel_x2.png");
  background-size: cover;
  height: 313px;
  margin-left: 4vw;
  position: relative;
  width: 1612px;
  top: -150px;

   @include respond-to('medium') {
    height: 313px;
    margin-left: 8%;
    top: -150px;
    width: 1612px;
  }

  @include respond-to('large') {
    margin-left: 3%;
  }

  @include respond-to('extralarge') {
    margin: 0 auto;
  }
}

#TV_personalisation {
  margin: 140px 0 100px 0;

  article {
    padding-bottom: 75px;

    @include respond-to('medium') {
      padding-bottom: 100px;
    }
  }

  video {
    @include border-radius (2px);
    background-color: #000;
    border: solid 2px #000;
    box-shadow:
    0 0 0 6px #333,
    0 0 0 7px #222;
    display: block;
    height: auto;
    margin: 0 auto;
    width: 100vw;
    max-height: 720px;
    max-width: 1280px;
    position: relative;

    @include respond-to('medium') {
     width: 60vw;
   }
 }

 #TV_personalisation_bottombg {
  background-color: #F9F9F9;
  bottom: -50px;
  height: 400px;
  width: 100%;
  position: absolute;
  z-index: -10;

  @include respond-to('medium') {
    height: 500px;
  }

  @include respond-to('large') {
   height: 400px;
 }
}
}
